var baseHost = 'https://api.darma.cn/mattress'
var imgUrl = 'http://www.hanjiaxin.cn/images/'
var deviceNos = '641743000773'

function formatTime(time) {
    if (typeof time !== 'number' || time < 0) {
        return time
    }

    var hour = parseInt(time / 3600)
    time = time % 3600
    var minute = parseInt(time / 60)
    time = time % 60
    var second = time

    return ([hour, minute, second]).map(function(n) {
        n = n.toString()
        return n[1] ? n : '0' + n
    }).join(':')
}

function formatLocation(longitude, latitude) {
    if (typeof longitude === 'string' && typeof latitude === 'string') {
        longitude = parseFloat(longitude)
        latitude = parseFloat(latitude)
    }

    longitude = longitude.toFixed(2)
    latitude = latitude.toFixed(2)

    return {
        longitude: longitude.toString().split('.'),
        latitude: latitude.toString().split('.')
    }
}
var dateUtils = {
    UNITS: {
        '': 31557600000,
        '': 2629800000,
        '': 86400000,
        '': 3600000,
        '': 60000,
        '': 1000
    },
    humanize: function(milliseconds) {
        var humanize = '';
        for (var key in this.UNITS) {
            if (milliseconds >= this.UNITS[key]) {
                humanize = Math.floor(milliseconds / this.UNITS[key]) + key + ''
                break
            }
        }
        return humanize || '';
    },
    format: function(dateStr) {
        var date = this.parse(dateStr)
        var diff = Date.now() - date.getTime()
        if (diff < this.UNITS['']) {
            return this.humanize(diff)
        }
        var _format = function(number) {
            return (number < 10 ? ('0' + number) : number)
        };
        return date.getFullYear() + '/' + _format(date.getMonth() + 1) + '/' + _format(date.getDay()) + '-' +
            _format(date.getHours()) + ':' + _format(date.getMinutes())
    },
    parse: function(str) { //"yyyy-mm-dd HH:MM:ss"Date
        var a = str.split(/[^0-9]/)
        return new Date(a[0], a[1] - 1, a[2], a[3], a[4], a[5])
    }
}

/**
 * local storage
 * @param key
 * @param value
 */
function setCookie(key, value) {
    uni.setStorageSync(key, value)
}
function setStorage(key, value) {
    uni.setStorageSync(key, value)
}

/**
 * local storage
 * @param key
 * @returns {*}
 */
function getCookie(key) {
    return uni.getStorageSync(key)
}
function getStorage(key) {
    return uni.getStorageSync(key)
}

/**
 *   ()
 * @param type  post,get,put,delete
 * @param url 
 * @param data 
 * @param res 
 * @param reg 
 */
function myAjax(type, url, data, res, reg) {
    uni.request({
        url: baseHost + url,
        data,
        header: {
            'content-type': 'application/json'
        },
        method: type,
        success(data) {
            res && res(data.data)
        },
        fail(data) {
            reg && reg(data.data)
        }
    })
}

/**
 *   ()
 * @param type  post,get,put,delete
 * @param url 
 * @param data 
 * @param res 
 * @param reg 
 */
function myAjax2(type, url, data, res, reg) {
    let accessToken = getCookie('accessToken')
    if (!accessToken) {
        uni.redirectTo({
            url: '../login/index'
        })
    }
    uni.request({
        url: baseHost + url,
        data,
        header: {
            'content-type': 'application/json',
            'accessToken': accessToken
        },
        method: type,
        success(data) {
            res && res(data.data)
        },
        fail(data) {
            reg && reg(data.data)
        }
    })
}

module.exports = {
    formatTime,
    formatLocation,
    dateUtils,
    setCookie,
    getCookie,
    setStorage,
    getStorage,
    myAjax,
    myAjax2
}
